import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Heading, Text, Link, Icon, Box, Spinner } from '@chakra-ui/core'
import DrawerOption from './drawer'
import DesktopVisualizerLeft from './desktopVisualizerLeft'
import Plot from 'react-plotly.js'
import { useDispatch, useSelector } from 'react-redux'
import * as actionTypes from '../redux/actions/actionTypes'

async function fetchData(url) {
  const historical = 'https://corona.lmao.ninja/v2/historical?lastdays=all'
  const corona_api = 'https://corona-api.com/countries?include=timeline'
  try {
    const api = url === 'old' ? historical : corona_api
    let response = await axios.get(api)
    // console.log(response.data);
    return response.data
  } catch (e) {
    console.log(`Failed to fetch countries: ${e.message}`, e)
    return
  }
}

export default function Graph() {
  const dispatch = useDispatch()
  const plotData = useSelector((state) => state.plotReducer.plotData)
  const [countryData, addData] = useState(null)

  const getData = async () => {
    const data = await fetchData('new').then((data) => data)
    await console.log(data)
    let obj = {}
    const old_api_func = () => {
      data.forEach((el) => {
        let country = el.country
        let province = el.province
        let timeline = el.timeline
        if (province) {
          // console.log('province :', country, province, obj)
          obj[country] = { ...obj[country] }
          obj[country]['province'] = { ...obj[country]['province'] }

          obj[country]['province'][province] = { province, timeline }
        } else obj[country] = timeline
      })
    }
    const new_api_func = () => {
      data.data.forEach((el) => {
        let country = el.name
        let keys_arr = Object.keys(el)
        let countryObj = {}
        keys_arr.forEach((key) => {
          if (key === 'timeline') {
            let timelineArr = el[key]
            let timelineObj = {}
            const dataByDate = {}
            let keys = timelineArr[1] ? Object.keys(timelineArr[1]) : null
            if (keys)
              keys.forEach((key) => {
                timelineObj[key] = timelineArr
                  .map((obj) => {
                    if (key === 'date') {
                      dataByDate[obj.date] = obj
                    }
                    return obj[key]
                  })
                  .reverse()
              })
            timelineObj['dataByDate'] = dataByDate
            countryObj[key] = timelineObj
          } else countryObj[key] = el[key]
        })
        obj[country] = { ...countryObj }
      })
    }
    await new_api_func()
    await addData(obj)
    dispatch({ type: actionTypes.SET_DATA, payload: obj })
  }

  useEffect(() => {
    console.log('calling get data useffect')
    getData()
  }, [])

  return (
    <div className='visualize-comp'>
      <>
        <div className='visualise-div'>
          <div className='left-option-div'>
            {countryData ? (
              <DrawerOption key='drawer-option' />
            ) : (
              <>
                'Fetching Data'
                <Spinner
                  thickness='4px'
                  speed='0.65s'
                  emptyColor='gray.200'
                  color='blue.500'
                  size='xl'
                />
              </>
            )}
          </div>
          <Box d='flex' width='full' height='96%' flexDirection='row' bg='#485'>
            <Box width='20%'>
              <DesktopVisualizerLeft />
            </Box>
            <Box
              d='flex'
              flexDirection='column'
              width='80%'
              height='100%'
              bg='#999'
              pr={0}
            >
              <Plot
                data={plotData.data}
                layout={{
                  ...plotData.layout,
                  type: 'date',
                  barmode: 'group',
                  autosize: true,
                  // width: window.innerWidth * 0.8,
                  height: window.innerHeight * 0.96,
                  // margin: {
                  //   l: 50,
                  //   r: 50,
                  //   b: 100,
                  //   t: 100,
                  //   pad: 4,
                  // },
                }}
                // frames={this.state.frames}
                config={{
                  ...plotData.config,
                  displayModeBar: true,
                  displaylogo: false,
                  scrollZoom: true,
                  responsive: true,
                  useResizeHandler: true,
                  style: { width: '100%', height: '100%' },
                  toImageButtonOptions: {
                    format: 'jpeg',
                    scale: 3,
                  },
                }}
                // onInitialized={figure => this.setState(figure)}
                // onUpdate={figure => this.setState(figure)}
              />
            </Box>
          </Box>
        </div>
      </>
    </div>
  )
}
